import React, { useState, useRef, useEffect } from 'react';
import { ChevronDown, X, Check } from 'lucide-react';
import { clsx } from 'clsx';

interface Option {
  value: string;
  label: string;
}

interface MultiSelectProps {
  options: Option[];
  selectedValues: string[];
  onChange: (values: string[]) => void;
  placeholder?: string;
  label?: string;
}

export const MultiSelect: React.FC<MultiSelectProps> = ({ 
  options, 
  selectedValues, 
  onChange, 
  placeholder = 'Selecione...',
  label
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (containerRef.current && !containerRef.current.contains(event.target as Node)) {
        setIsOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  const toggleOption = (value: string) => {
    const newValues = selectedValues.includes(value)
      ? selectedValues.filter(v => v !== value)
      : [...selectedValues, value];
    onChange(newValues);
  };

  const removeValue = (e: React.MouseEvent, value: string) => {
    e.stopPropagation();
    onChange(selectedValues.filter(v => v !== value));
  };

  const selectedLabels = options
    .filter(opt => selectedValues.includes(opt.value))
    .map(opt => opt.label);

  return (
    <div className="relative" ref={containerRef}>
      {label && (
        <label className="block text-xs font-semibold text-slate-500 uppercase tracking-wider mb-1.5 ml-1">
          {label}
        </label>
      )}
      <div 
        className={clsx(
          "min-h-[46px] border rounded-xl shadow-sm bg-white px-3 py-2 cursor-pointer flex items-center justify-between transition-all duration-200",
          isOpen 
            ? "border-indigo-500 ring-2 ring-indigo-100" 
            : "border-slate-200 hover:border-slate-300 hover:bg-slate-50"
        )}
        onClick={() => setIsOpen(!isOpen)}
      >
        <div className="flex flex-wrap gap-1.5">
          {selectedValues.length === 0 && (
            <span className="text-slate-400 text-sm">{placeholder}</span>
          )}
          {selectedValues.length > 0 && (
            <div className="flex flex-wrap gap-1.5">
              {selectedValues.length <= 2 ? (
                selectedLabels.map((label, idx) => (
                  <span key={idx} className="inline-flex items-center px-2 py-0.5 rounded-md text-xs font-medium bg-indigo-50 text-indigo-700 border border-indigo-100">
                    {label}
                    <X 
                      className="ml-1 h-3 w-3 cursor-pointer hover:text-indigo-900 transition-colors" 
                      onClick={(e) => removeValue(e, selectedValues[idx])} // Note: logic assumes order match, better to use value if possible but labels derived from options preserves order
                    />
                  </span>
                ))
              ) : (
                <span className="inline-flex items-center px-2.5 py-1 rounded-md text-xs font-medium bg-indigo-100 text-indigo-800">
                  {selectedValues.length} selecionados
                </span>
              )}
            </div>
          )}
        </div>
        <ChevronDown className={clsx("h-4 w-4 text-slate-400 transition-transform duration-200", isOpen && "transform rotate-180 text-indigo-500")} />
      </div>

      {isOpen && (
        <div className="absolute z-50 mt-2 w-full bg-white shadow-xl shadow-slate-200/50 max-h-64 rounded-xl py-2 text-base ring-1 ring-black ring-opacity-5 overflow-auto focus:outline-none sm:text-sm animate-in fade-in zoom-in-95 duration-100 origin-top">
          {options.map((option) => {
            const isSelected = selectedValues.includes(option.value);
            return (
              <div
                key={option.value}
                className={clsx(
                  "cursor-pointer select-none relative py-2.5 pl-4 pr-9 transition-colors",
                  isSelected ? "text-indigo-700 bg-indigo-50" : "text-slate-700 hover:bg-slate-50"
                )}
                onClick={() => toggleOption(option.value)}
              >
                <span className={clsx("block truncate", isSelected ? "font-semibold" : "font-normal")}>
                  {option.label}
                </span>
                {isSelected && (
                  <span className="absolute inset-y-0 right-0 flex items-center pr-4 text-indigo-600">
                    <Check className="h-4 w-4" />
                  </span>
                )}
              </div>
            );
          })}
        </div>
      )}
    </div>
  );
};
