export type ShiftType = 'MANHA' | 'TARDE' | 'NOITE';

export interface Brother {
  id: string;
  name: string;
  constraints: {
    maxPerMonth?: number;
    fixedPerMonth?: number;
    daysAllowed: number[]; // 0=Sun, 1=Mon, ..., 6=Sat
    shiftsAllowed: ShiftType[];
    specificDays?: {
      dayOfWeek: number;
      shift: ShiftType;
      count: number;
    }[];
    forbiddenDays?: number[];
    forbiddenShifts?: ShiftType[];
  };
}

export interface Shift {
  id: string;
  date: Date;
  type: ShiftType;
  assignedBrothers: string[]; // Brother IDs
}

export interface Schedule {
  shifts: Shift[];
}

