import React, { useState, useEffect, useMemo } from 'react';
import { generateSchedule } from './utils/scheduler';
import { Shift, BROTHERS } from './types/scheduler';
import { ScheduleTable } from './components/ScheduleTable';
import { StatsView } from './components/StatsView';
import { MultiSelect } from './components/MultiSelect';
import { DateSearch } from './components/DateSearch';
import { Calendar, Download, Filter, X, LayoutGrid, BarChart3 } from 'lucide-react';
import { format, parseISO } from 'date-fns';
import { ptBR } from 'date-fns/locale';
import { clsx } from 'clsx';

function App() {
  const [shifts, setShifts] = useState<Shift[]>([]);
  const [selectedBrotherIds, setSelectedBrotherIds] = useState<string[]>([]);
  const [selectedMonthStrs, setSelectedMonthStrs] = useState<string[]>([]);
  const [dateSearchQuery, setDateSearchQuery] = useState('');
  const [dateRange, setDateRange] = useState<{ start: Date | null; end: Date | null } | null>(null);
  const [view, setView] = useState<'schedule' | 'stats'>('schedule');
  const [showFilters, setShowFilters] = useState(false);

  useEffect(() => {
    const newShifts = generateSchedule();
    setShifts(newShifts);
  }, []);

  const months = useMemo(() => {
    return Array.from(new Set(shifts.map(s => s.date.toISOString().slice(0, 7)))).sort();
  }, [shifts]);

  const brotherOptions = useMemo(() => BROTHERS.map(b => ({ value: b.id, label: b.name })), []);
  const monthOptions = useMemo(() => months.map(m => ({ 
    value: parseISO(m).toISOString(),
    label: format(parseISO(m), 'MMMM yyyy', { locale: ptBR }).replace(/^\w/, c => c.toUpperCase())
  })), [months]);

  const activeFiltersCount = selectedBrotherIds.length + selectedMonthStrs.length + (dateSearchQuery ? 1 : 0) + (dateRange ? 1 : 0);

  const clearFilters = () => {
    setSelectedBrotherIds([]);
    setSelectedMonthStrs([]);
    setDateSearchQuery('');
    setDateRange(null);
  };

  return (
    <div className="min-h-screen bg-slate-50 font-sans text-slate-900 pb-20 selection:bg-indigo-100 selection:text-indigo-900">
      {/* Modern Header */}
      <header className="bg-white/80 backdrop-blur-md border-b border-slate-200 sticky top-0 z-40 transition-all duration-300">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 h-16 md:h-20 flex items-center justify-between gap-4">
          <div className="flex items-center gap-3">
            <div className="bg-indigo-600 p-2 rounded-xl shadow-lg shadow-indigo-200">
              <Calendar className="h-5 w-5 md:h-6 md:w-6 text-white" />
            </div>
            <div>
              <h1 className="text-lg md:text-xl font-bold text-slate-900 tracking-tight leading-none">
                Escala Porteiros
              </h1>
              <p className="text-xs text-slate-500 font-medium mt-0.5">Ano 2027</p>
            </div>
          </div>

          <div className="flex items-center gap-2 md:gap-4">
            {/* View Toggles - Desktop */}
            <div className="hidden md:flex bg-slate-100 p-1 rounded-lg border border-slate-200">
              <button 
                onClick={() => setView('schedule')}
                className={clsx(
                  "flex items-center gap-2 px-4 py-1.5 rounded-md text-sm font-medium transition-all duration-200",
                  view === 'schedule' 
                    ? 'bg-white text-indigo-600 shadow-sm' 
                    : 'text-slate-500 hover:text-slate-700'
                )}
              >
                <LayoutGrid className="h-4 w-4" />
                Escala
              </button>
              <button 
                onClick={() => setView('stats')}
                className={clsx(
                  "flex items-center gap-2 px-4 py-1.5 rounded-md text-sm font-medium transition-all duration-200",
                  view === 'stats' 
                    ? 'bg-white text-indigo-600 shadow-sm' 
                    : 'text-slate-500 hover:text-slate-700'
                )}
              >
                <BarChart3 className="h-4 w-4" />
                Estatsticas
              </button>
            </div>

            {/* Mobile Filter Toggle */}
            <button 
              className="md:hidden p-2.5 rounded-xl bg-white border border-slate-200 text-slate-600 shadow-sm relative active:scale-95 transition-transform"
              onClick={() => setShowFilters(!showFilters)}
            >
              <Filter className="h-5 w-5" />
              {activeFiltersCount > 0 && (
                <span className="absolute -top-1 -right-1 bg-indigo-500 text-white text-[10px] font-bold rounded-full h-4 w-4 flex items-center justify-center shadow-sm">
                  {activeFiltersCount}
                </span>
              )}
            </button>
          </div>
        </div>
        
        {/* Mobile View Tabs */}
        <div className="md:hidden border-t border-slate-100 px-4">
          <div className="flex">
            <button 
              onClick={() => setView('schedule')}
              className={clsx(
                "flex-1 py-3 text-sm font-medium border-b-2 transition-colors",
                view === 'schedule' ? "border-indigo-600 text-indigo-600" : "border-transparent text-slate-500"
              )}
            >
              Escala
            </button>
            <button 
              onClick={() => setView('stats')}
              className={clsx(
                "flex-1 py-3 text-sm font-medium border-b-2 transition-colors",
                view === 'stats' ? "border-indigo-600 text-indigo-600" : "border-transparent text-slate-500"
              )}
            >
              Estatsticas
            </button>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Filters Section */}
        <div className={clsx(
          "bg-white rounded-2xl shadow-sm border border-slate-200 p-5 md:p-6 mb-8 transition-all duration-300 ease-in-out",
          showFilters ? "block" : "hidden md:block"
        )}>
          <div className="flex flex-col lg:flex-row gap-6 lg:items-end justify-between">
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-5 w-full lg:w-auto flex-1">
              <div className="w-full">
                <DateSearch 
                  value={dateSearchQuery}
                  onSearch={setDateSearchQuery}
                  onDateRangeSelect={setDateRange}
                  currentRange={dateRange}
                />
              </div>

              <div className="w-full">
                <MultiSelect 
                  label="Filtrar Irmos"
                  options={brotherOptions}
                  selectedValues={selectedBrotherIds}
                  onChange={setSelectedBrotherIds}
                  placeholder="Selecione irmos..."
                />
              </div>

              <div className="w-full">
                <MultiSelect 
                  label="Filtrar Meses"
                  options={monthOptions}
                  selectedValues={selectedMonthStrs}
                  onChange={setSelectedMonthStrs}
                  placeholder="Selecione meses..."
                />
              </div>
            </div>

            <div className="flex flex-col sm:flex-row gap-3 w-full lg:w-auto pt-4 lg:pt-0 border-t lg:border-t-0 border-slate-100 lg:pl-6 lg:border-l">
              {activeFiltersCount > 0 && (
                <button 
                  onClick={clearFilters}
                  className="flex items-center justify-center gap-2 px-4 py-2.5 text-rose-600 hover:bg-rose-50 rounded-xl transition-colors text-sm font-medium"
                >
                  <X className="h-4 w-4" />
                  Limpar
                </button>
              )}
              <button 
                onClick={() => window.print()}
                className="flex-1 lg:flex-none flex items-center justify-center gap-2 px-5 py-2.5 bg-white border border-slate-200 text-slate-700 rounded-xl hover:bg-slate-50 hover:border-slate-300 active:scale-95 transition-all text-sm font-medium"
              >
                <Download className="h-4 w-4" />
                <span className="hidden sm:inline">Exportar PDF</span>
                <span className="sm:hidden">PDF</span>
              </button>
            </div>
          </div>
        </div>

        {/* Content */}
        <div className="animate-in fade-in slide-in-from-bottom-4 duration-500">
          {view === 'schedule' ? (
            <ScheduleTable 
              shifts={shifts} 
              selectedBrotherIds={selectedBrotherIds}
              selectedMonthStrs={selectedMonthStrs}
              dateSearchQuery={dateSearchQuery}
              dateRange={dateRange}
            />
          ) : (
            <StatsView shifts={shifts} />
          )}
        </div>
      </main>
    </div>
  );
}

export default App;
