import { 
  eachDayOfInterval, 
  getDay, 
  isSameDay, 
  startOfMonth, 
  endOfMonth, 
  isSameMonth, 
  addDays, 
  differenceInDays,
  getDate,
  isTuesday,
  isSaturday
} from 'date-fns';
import { Brother, Shift, ShiftType, BROTHERS } from '../types/scheduler';

// Helper to check if a date is the first occurrence of that day in the month
function isFirstDayOfWeekInMonth(date: Date, dayOfWeek: number): boolean {
  return getDay(date) === dayOfWeek && getDate(date) <= 7;
}

export function generateEmptySchedule(year: number): Shift[] {
  const startDate = new Date(year, 0, 1); // Jan 1st
  const endDate = new Date(year, 11, 31); // Dec 31st
  
  const allDays = eachDayOfInterval({ start: startDate, end: endDate });
  const shifts: Shift[] = [];
  let shiftIdCounter = 1;

  allDays.forEach(date => {
    const dayOfWeek = getDay(date); // 0=Sun, ..., 6=Sat

    // Sunday: Manh, Noite
    if (dayOfWeek === 0) {
      shifts.push({ id: `shift-${shiftIdCounter++}`, date, type: 'MANHA', assignedBrothers: [] });
      shifts.push({ id: `shift-${shiftIdCounter++}`, date, type: 'NOITE', assignedBrothers: [] });
    }
    
    // Tuesday: 1st Tuesday of month -> Noite
    if (dayOfWeek === 2 && isFirstDayOfWeekInMonth(date, 2)) {
      shifts.push({ id: `shift-${shiftIdCounter++}`, date, type: 'NOITE', assignedBrothers: [] });
    }

    // Wednesday: All -> Noite
    if (dayOfWeek === 3) {
      shifts.push({ id: `shift-${shiftIdCounter++}`, date, type: 'NOITE', assignedBrothers: [] });
    }

    // Saturday
    if (dayOfWeek === 6) {
      // 1st Saturday: Tarde, Noite
      if (isFirstDayOfWeekInMonth(date, 6)) {
        shifts.push({ id: `shift-${shiftIdCounter++}`, date, type: 'TARDE', assignedBrothers: [] });
        shifts.push({ id: `shift-${shiftIdCounter++}`, date, type: 'NOITE', assignedBrothers: [] });
      } else {
        // Other Saturdays: Noite only
        shifts.push({ id: `shift-${shiftIdCounter++}`, date, type: 'NOITE', assignedBrothers: [] });
      }
    }
  });

  return shifts;
}

function validateSchedule(shifts: Shift[], brothers: Brother[]) {
  console.group('Validao da Escala 2027');
  let valid = true;

  // 1. Check Mazino (1x/month, Sun Night)
  const mazino = brothers.find(b => b.id === 'mazino');
  if (mazino) {
    const assignments = shifts.filter(s => s.assignedBrothers.includes('mazino'));
    const byMonth: Record<string, number> = {};
    assignments.forEach(s => {
      const m = s.date.toISOString().slice(0, 7);
      byMonth[m] = (byMonth[m] || 0) + 1;
      if (getDay(s.date) !== 0 || s.type !== 'NOITE') {
        console.error(` Mazino em dia/turno errado: ${s.date.toISOString()} - ${s.type}`);
        valid = false;
      }
    });
    Object.entries(byMonth).forEach(([m, count]) => {
      if (count !== 1) {
        console.error(` Mazino tem ${count} turnos em ${m} (esperado: 1)`);
        valid = false;
      }
    });
  }

  // 2. Check Thiago (2x/month, Wed Night)
  const thiago = brothers.find(b => b.id === 'thiago');
  if (thiago) {
    const assignments = shifts.filter(s => s.assignedBrothers.includes('thiago'));
    const byMonth: Record<string, number> = {};
    assignments.forEach(s => {
      const m = s.date.toISOString().slice(0, 7);
      byMonth[m] = (byMonth[m] || 0) + 1;
      if (getDay(s.date) !== 3 || s.type !== 'NOITE') {
        console.error(` Thiago em dia/turno errado: ${s.date.toISOString()} - ${s.type}`);
        valid = false;
      }
    });
    Object.entries(byMonth).forEach(([m, count]) => {
      if (count !== 2) {
        console.error(` Thiago tem ${count} turnos em ${m} (esperado: 2)`);
        valid = false;
      }
    });
  }

  // 3. Check Williams (3x/month)
  const williams = brothers.find(b => b.id === 'williams');
  if (williams) {
    const assignments = shifts.filter(s => s.assignedBrothers.includes('williams'));
    const byMonth: Record<string, number> = {};
    assignments.forEach(s => {
      const m = s.date.toISOString().slice(0, 7);
      byMonth[m] = (byMonth[m] || 0) + 1;
    });
    Object.entries(byMonth).forEach(([m, count]) => {
      if (count !== 3) {
        console.error(` Williams tem ${count} turnos em ${m} (esperado: 3)`);
        valid = false;
      }
    });
  }

  // 4. Check Adilson (Sun Night only)
  const adilson = brothers.find(b => b.id === 'adilson');
  if (adilson) {
    const assignments = shifts.filter(s => s.assignedBrothers.includes('adilson'));
    assignments.forEach(s => {
      if (getDay(s.date) !== 0 || s.type !== 'NOITE') {
        console.error(` Adilson em dia/turno errado: ${s.date.toISOString()} - ${s.type}`);
        valid = false;
      }
    });
  }

  // 5. Check Eduardo/Elson/Henrique (No Wed/Tue)
  ['eduardo', 'elson', 'henrique'].forEach(id => {
    const assignments = shifts.filter(s => s.assignedBrothers.includes(id));
    assignments.forEach(s => {
      const day = getDay(s.date);
      if (day === 2 || day === 3) {
        console.error(` ${id} escalado em Tera/Quarta: ${s.date.toISOString()}`);
        valid = false;
      }
    });
  });

  if (valid) {
    console.log(' TODAS AS REGRAS FORAM VALIDADAS COM SUCESSO!');
  } else {
    console.error(' FALHA NA VALIDAO DE REGRAS');
  }
  console.groupEnd();
}

export function generateSchedule(): Shift[] {
  let shifts = generateEmptySchedule(2027);
  const brothers = [...BROTHERS];
  
  // Track assignments
  const assignments: Record<string, Shift[]> = {};
  brothers.forEach(b => assignments[b.id] = []);

  // Helper to get assignments in a month
  const getMonthAssignments = (brotherId: string, date: Date) => {
    return assignments[brotherId].filter(s => isSameMonth(s.date, date));
  };

  // Helper to check if brother is available
  const isAvailable = (brother: Brother, shift: Shift) => {
    // Check day allowed
    const day = getDay(shift.date);
    if (!brother.constraints.daysAllowed.includes(day)) return false;
    if (brother.constraints.forbiddenDays?.includes(day)) return false;

    // Check shift allowed
    if (!brother.constraints.shiftsAllowed.includes(shift.type)) return false;

    // Check specific constraints
    // Mazino: Sun Night only (already covered by daysAllowed/shiftsAllowed but double check logic if needed)
    
    // Check if already assigned today
    const assignedToday = assignments[brother.id].some(s => isSameDay(s.date, shift.date));
    if (assignedToday) return false;

    return true;
  };

  // 1. Assign Fixed/Restricted Roles First
  
  // Mazino: 1x/month, Sun Night
  const mazino = brothers.find(b => b.id === 'mazino')!;
  fillFixed(shifts, mazino, 1, (s) => getDay(s.date) === 0 && s.type === 'NOITE', assignments);

  // Thiago: 2x/month, Wed Night
  const thiago = brothers.find(b => b.id === 'thiago')!;
  fillFixed(shifts, thiago, 2, (s) => getDay(s.date) === 3 && s.type === 'NOITE', assignments);

  // Williams: 3x/month, Any
  // We will treat Williams as a high priority in the general fill, or pre-fill him distributedly.
  // Let's pre-fill Williams to ensure he gets exactly 3.
  const williams = brothers.find(b => b.id === 'williams')!;
  fillFixed(shifts, williams, 3, (s) => true, assignments); // Any valid slot

  // 2. General Fill
  // Sort shifts chronologically? Yes.
  // We need to fill remaining slots (up to 3 per shift)
  
  for (const shift of shifts) {
    while (shift.assignedBrothers.length < 3) {
      // Find candidates
      const candidates = brothers.filter(b => {
        if (shift.assignedBrothers.includes(b.id)) return false;
        if (!isAvailable(b, shift)) return false;
        
        // Check monthly limits
        const monthCount = getMonthAssignments(b.id, shift.date).length;
        if (b.constraints.fixedPerMonth && monthCount >= b.constraints.fixedPerMonth) return false;
        if (b.constraints.maxPerMonth && monthCount >= b.constraints.maxPerMonth) return false;

        return true;
      });

      if (candidates.length === 0) {
        // console.warn(`No candidates for shift ${shift.id} on ${shift.date}`);
        break; // Cannot fill
      }

      // Score candidates
      // 1. Spacing (days since last assignment) - Higher is better
      // 2. Total assignments count - Lower is better
      // 3. Monthly assignments count - Lower is better
      
      candidates.sort((a, b) => {
        const lastA = assignments[a.id].length > 0 ? assignments[a.id][assignments[a.id].length - 1].date : new Date(2026, 0, 1);
        const lastB = assignments[b.id].length > 0 ? assignments[b.id][assignments[b.id].length - 1].date : new Date(2026, 0, 1);
        
        const distA = differenceInDays(shift.date, lastA);
        const distB = differenceInDays(shift.date, lastB);

        // Prefer larger distance
        if (Math.abs(distA - distB) > 2) return distB - distA;

        // Prefer lower total count
        const totalA = assignments[a.id].length;
        const totalB = assignments[b.id].length;
        if (totalA !== totalB) return totalA - totalB;

        return 0;
      });

      // Pick best
      const best = candidates[0];
      shift.assignedBrothers.push(best.id);
      assignments[best.id].push(shift);
      // Sort assignments by date for correct distance calc next time
      assignments[best.id].sort((a, b) => a.date.getTime() - b.date.getTime());
    }
  }

  // Run validation
  validateSchedule(shifts, brothers);

  return shifts;
}

function fillFixed(
  allShifts: Shift[], 
  brother: Brother, 
  countPerMonth: number, 
  filterFn: (s: Shift) => boolean,
  assignments: Record<string, Shift[]>
) {
  // Group shifts by month
  const months = new Set(allShifts.map(s => startOfMonth(s.date).toISOString()));
  
  months.forEach(monthStr => {
    const monthStart = new Date(monthStr);
    const monthShifts = allShifts.filter(s => 
      isSameMonth(s.date, monthStart) && 
      filterFn(s) &&
      s.assignedBrothers.length < 3 &&
      !assignments[brother.id].some(assigned => isSameDay(assigned.date, s.date)) // Not already assigned today
    );

    // Randomly pick 'countPerMonth' shifts
    // Try to space them out if count > 1
    let picked = 0;
    // Simple random shuffle for now, or spaced selection
    const shuffled = [...monthShifts].sort(() => Math.random() - 0.5);
    
    for (const shift of shuffled) {
      if (picked >= countPerMonth) break;
      
      // Double check availability (in case of conflict with other fixed assignments logic, though here we are first)
      if (shift.assignedBrothers.length < 3) {
         // Check spacing if we already picked one this month?
         // For simplicity, just pick. The general fill handles the rest.
         shift.assignedBrothers.push(brother.id);
         assignments[brother.id].push(shift);
         picked++;
      }
    }
  });
  // Sort assignments
  assignments[brother.id].sort((a, b) => a.date.getTime() - b.date.getTime());
}
