import React, { useState, useRef, useEffect } from 'react';
import { Calendar as CalendarIcon, Search, X } from 'lucide-react';
import { startOfWeek, endOfWeek, startOfMonth, endOfMonth, isSameDay, format } from 'date-fns';
import { ptBR } from 'date-fns/locale';
import { clsx } from 'clsx';

interface DateSearchProps {
  value: string;
  onSearch: (query: string) => void;
  onDateRangeSelect: (range: { start: Date | null; end: Date | null } | null) => void;
  currentRange: { start: Date | null; end: Date | null } | null;
}

export const DateSearch: React.FC<DateSearchProps> = ({ value, onSearch, onDateRangeSelect, currentRange }) => {
  // Sync query with currentRange if it changes externally or via quick actions
  useEffect(() => {
    if (currentRange?.start && currentRange?.end) {
      if (isSameDay(currentRange.start, currentRange.end)) {
        onSearch(format(currentRange.start, 'dd/MM/yyyy'));
      } else {
        onSearch(`${format(currentRange.start, 'dd/MM')} - ${format(currentRange.end, 'dd/MM')}`);
      }
    }
  }, [currentRange]);

  const handleTextChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const val = e.target.value;
    onSearch(val);
    if (val) {
      onDateRangeSelect(null); 
    }
  };

  const handleQuickAction = (type: 'today' | 'week' | 'month') => {
    const today = new Date();
    let start: Date, end: Date;

    switch (type) {
      case 'today':
        start = today;
        end = today;
        break;
      case 'week':
        start = startOfWeek(today, { locale: ptBR });
        end = endOfWeek(today, { locale: ptBR });
        break;
      case 'month':
        start = startOfMonth(today);
        end = endOfMonth(today);
        break;
    }

    onDateRangeSelect({ start, end });
    // Query update handled by useEffect
  };

  const handleDateChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.value) {
      // Parse as local date to avoid timezone issues
      const [year, month, day] = e.target.value.split('-').map(Number);
      const date = new Date(year, month - 1, day);
      
      onDateRangeSelect({ start: date, end: date });
      // Query update handled by useEffect
    }
  };

  return (
    <div className="flex flex-col gap-3 w-full">
      <label className="block text-xs font-semibold text-slate-500 uppercase tracking-wider ml-1">
        Buscar Data ou Dia
      </label>
      
      <div className="relative group">
        <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
          <Search className="h-4 w-4 text-slate-400 group-focus-within:text-indigo-500 transition-colors" />
        </div>
        
        <input
          type="text"
          className="block w-full pl-10 pr-12 py-2.5 border border-slate-200 rounded-xl leading-5 bg-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-indigo-100 focus:border-indigo-500 sm:text-sm transition-all shadow-sm"
          placeholder="Ex: 15/03 ou Domingo"
          value={value}
          onChange={handleTextChange}
        />

        <div className="absolute inset-y-0 right-0 flex items-center pr-2 gap-1 z-20">
          {value && (
            <button
              onClick={() => { onSearch(''); }}
              className="p-1 text-slate-400 hover:text-slate-600 rounded-full hover:bg-slate-100 transition-colors"
            >
              <X className="h-4 w-4" />
            </button>
          )}
          
          <div className="relative w-8 h-8 flex items-center justify-center">
            <CalendarIcon className="h-5 w-5 text-slate-400 pointer-events-none" />
            <input
              type="date"
              className="absolute inset-0 opacity-0 cursor-pointer w-full h-full"
              onChange={handleDateChange}
              title="Selecionar data no calendrio"
            />
          </div>
        </div>
      </div>

      <div className="flex flex-wrap gap-2">
        <button
          onClick={() => handleQuickAction('today')}
          className={clsx(
            "px-3 py-1.5 text-xs font-medium rounded-lg border transition-all active:scale-95",
            currentRange?.start && isSameDay(currentRange.start, new Date()) && isSameDay(currentRange.end!, new Date())
              ? "bg-indigo-600 text-white border-indigo-600 shadow-sm shadow-indigo-200"
              : "bg-white text-slate-600 border-slate-200 hover:bg-slate-50 hover:border-slate-300"
          )}
        >
          Hoje
        </button>
        <button
          onClick={() => handleQuickAction('week')}
          className={clsx(
            "px-3 py-1.5 text-xs font-medium rounded-lg border transition-all active:scale-95",
            currentRange?.end && !isSameDay(currentRange.start!, currentRange.end) && isSameDay(currentRange.end, endOfWeek(new Date(), { locale: ptBR }))
              ? "bg-indigo-600 text-white border-indigo-600 shadow-sm shadow-indigo-200"
              : "bg-white text-slate-600 border-slate-200 hover:bg-slate-50 hover:border-slate-300"
          )}
        >
          Esta Semana
        </button>
        <button
          onClick={() => handleQuickAction('month')}
          className={clsx(
            "px-3 py-1.5 text-xs font-medium rounded-lg border transition-all active:scale-95",
            currentRange?.end && isSameDay(currentRange.end, endOfMonth(new Date()))
              ? "bg-indigo-600 text-white border-indigo-600 shadow-sm shadow-indigo-200"
              : "bg-white text-slate-600 border-slate-200 hover:bg-slate-50 hover:border-slate-300"
          )}
        >
          Este Ms
        </button>
      </div>
    </div>
  );
};
