import React, { useMemo } from 'react';
import { Shift, BROTHERS } from '../types/scheduler';
import { clsx } from 'clsx';
import { BarChart3, TrendingUp } from 'lucide-react';

interface StatsViewProps {
  shifts: Shift[];
}

export const StatsView: React.FC<StatsViewProps> = ({ shifts }) => {
  const stats = useMemo(() => {
    const counts: Record<string, { total: number; byMonth: Record<string, number> }> = {};
    
    BROTHERS.forEach(b => {
      counts[b.id] = { total: 0, byMonth: {} };
    });

    shifts.forEach(shift => {
      const monthKey = shift.date.toISOString().slice(0, 7); // YYYY-MM
      shift.assignedBrothers.forEach(bId => {
        if (counts[bId]) {
          counts[bId].total++;
          counts[bId].byMonth[monthKey] = (counts[bId].byMonth[monthKey] || 0) + 1;
        }
      });
    });

    return counts;
  }, [shifts]);

  const months = useMemo(() => {
    const m = new Set<string>();
    shifts.forEach(s => m.add(s.date.toISOString().slice(0, 7)));
    return Array.from(m).sort();
  }, [shifts]);

  return (
    <div className="bg-white rounded-2xl shadow-sm border border-slate-200 overflow-hidden">
      <div className="bg-slate-50/50 px-6 py-4 border-b border-slate-100 flex items-center gap-3">
        <div className="bg-white p-2 rounded-lg shadow-sm border border-slate-100">
          <BarChart3 className="h-5 w-5 text-indigo-600" />
        </div>
        <div>
          <h3 className="text-lg font-bold text-slate-800 tracking-tight">
            Estatsticas de Distribuio
          </h3>
          <p className="text-xs text-slate-500 font-medium">
            Total de turnos por irmo e ms
          </p>
        </div>
      </div>

      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-slate-100 text-sm">
          <thead className="bg-slate-50/50">
            <tr>
              <th className="px-4 py-3 text-left font-semibold text-slate-500 uppercase tracking-wider text-xs sticky left-0 bg-slate-50 z-10">Irmo</th>
              <th className="px-4 py-3 text-center font-semibold text-slate-500 uppercase tracking-wider text-xs bg-indigo-50/30 text-indigo-700">Total</th>
              {months.map(m => (
                <th key={m} className="px-2 py-3 text-center font-semibold text-slate-500 uppercase tracking-wider text-[10px]">
                  {m.split('-')[1]}
                </th>
              ))}
            </tr>
          </thead>
          <tbody className="divide-y divide-slate-50 bg-white">
            {BROTHERS.map((brother, idx) => {
              const s = stats[brother.id];
              return (
                <tr key={brother.id} className={clsx(
                  "hover:bg-slate-50/80 transition-colors",
                  idx % 2 === 0 ? "bg-white" : "bg-slate-50/30"
                )}>
                  <td className="px-4 py-3 font-medium text-slate-700 sticky left-0 bg-inherit border-r border-slate-100 shadow-[2px_0_5px_-2px_rgba(0,0,0,0.05)]">
                    {brother.name}
                  </td>
                  <td className="px-4 py-3 text-center font-bold text-indigo-600 bg-indigo-50/10">
                    {s.total}
                  </td>
                  {months.map(m => (
                    <td key={m} className={clsx(
                      "px-2 py-3 text-center transition-colors",
                      s.byMonth[m] === 0 ? "text-slate-200" : "text-slate-600 font-medium"
                    )}>
                      {s.byMonth[m] || 0}
                    </td>
                  ))}
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
};
