import React, { useMemo } from 'react';
import { format, isSameMonth, parseISO, startOfMonth, isWithinInterval, isSameDay } from 'date-fns';
import { ptBR } from 'date-fns/locale';
import { Shift, BROTHERS } from '../types/scheduler';
import { clsx } from 'clsx';
import { Calendar, Clock, Users, Sun, Moon, Sunset } from 'lucide-react';

interface ScheduleTableProps {
  shifts: Shift[];
  selectedBrotherIds: string[];
  selectedMonthStrs: string[];
  dateSearchQuery?: string;
  dateRange?: { start: Date | null; end: Date | null } | null;
}

export const ScheduleTable: React.FC<ScheduleTableProps> = ({ 
  shifts, 
  selectedBrotherIds, 
  selectedMonthStrs,
  dateSearchQuery,
  dateRange
}) => {
  const filteredShifts = useMemo(() => {
    return shifts.filter(shift => {
      // 1. Date Range Filter
      if (dateRange?.start && dateRange?.end) {
        if (!isWithinInterval(shift.date, { start: dateRange.start, end: dateRange.end })) {
          return false;
        }
      }

      // 2. Text Search Filter (Date or Day)
      if (dateSearchQuery) {
        const query = dateSearchQuery.toLowerCase();
        const dateStr = format(shift.date, 'dd/MM');
        const dayName = format(shift.date, 'EEEE', { locale: ptBR }).toLowerCase();
        
        if (!dateStr.includes(query) && !dayName.includes(query)) {
          return false;
        }
      }

      // 3. Month Filter
      if (selectedMonthStrs.length > 0) {
        const match = selectedMonthStrs.some(m => isSameMonth(parseISO(m), shift.date));
        if (!match) return false;
      }

      // 4. Brother Filter
      if (selectedBrotherIds.length > 0) {
        const hasBrother = shift.assignedBrothers.some(id => selectedBrotherIds.includes(id));
        if (!hasBrother) return false;
      }

      return true;
    });
  }, [shifts, selectedBrotherIds, selectedMonthStrs, dateSearchQuery, dateRange]);

  const months = useMemo(() => {
    const groups: Record<string, Shift[]> = {};
    filteredShifts.forEach(shift => {
      const key = startOfMonth(shift.date).toISOString();
      if (!groups[key]) groups[key] = [];
      groups[key].push(shift);
    });
    return Object.entries(groups).sort((a, b) => a[0].localeCompare(b[0]));
  }, [filteredShifts]);

  const getBrotherName = (id: string) => BROTHERS.find(b => b.id === id)?.name || id;

  const ShiftBadge = ({ type }: { type: string }) => {
    const config = {
      'MANHA': { bg: 'bg-amber-50', text: 'text-amber-700', border: 'border-amber-100', icon: Sun },
      'TARDE': { bg: 'bg-orange-50', text: 'text-orange-700', border: 'border-orange-100', icon: Sunset },
      'NOITE': { bg: 'bg-indigo-50', text: 'text-indigo-700', border: 'border-indigo-100', icon: Moon },
    }[type] || { bg: 'bg-gray-50', text: 'text-gray-700', border: 'border-gray-100', icon: Clock };

    const Icon = config.icon;

    return (
      <span className={clsx(
        "px-2.5 py-1 inline-flex items-center gap-1.5 text-xs font-semibold rounded-full border",
        config.bg, config.text, config.border
      )}>
        <Icon className="h-3 w-3" />
        {type}
      </span>
    );
  };

  const BrotherTag = ({ id }: { id: string }) => (
    <span className={clsx(
      "px-2.5 py-1 rounded-lg text-xs font-medium border transition-colors",
      selectedBrotherIds.includes(id)
        ? "bg-indigo-600 text-white border-indigo-600 shadow-sm" 
        : "bg-white text-slate-600 border-slate-200"
    )}>
      {getBrotherName(id)}
    </span>
  );

  if (months.length === 0) {
    return (
      <div className="text-center py-16 bg-white rounded-2xl border border-slate-200 border-dashed">
        <div className="bg-slate-50 p-4 rounded-full inline-block mb-4">
          <Calendar className="h-8 w-8 text-slate-400" />
        </div>
        <h3 className="text-lg font-medium text-slate-900">Nenhum turno encontrado</h3>
        <p className="text-slate-500 mt-1">Tente ajustar os filtros selecionados.</p>
      </div>
    );
  }

  return (
    <div className="space-y-8">
      {months.map(([monthStr, monthShifts]) => (
        <div key={monthStr} className="bg-white rounded-2xl shadow-sm border border-slate-200 overflow-hidden">
          <div className="bg-slate-50/50 px-6 py-4 border-b border-slate-100 flex items-center gap-3 sticky top-0 z-10 backdrop-blur-sm">
            <div className="bg-white p-2 rounded-lg shadow-sm border border-slate-100">
              <Calendar className="h-5 w-5 text-indigo-600" />
            </div>
            <h3 className="text-lg font-bold text-slate-800 capitalize tracking-tight">
              {format(parseISO(monthStr), 'MMMM yyyy', { locale: ptBR })}
            </h3>
          </div>
          
          {/* Desktop View (Table) */}
          <div className="hidden md:block overflow-x-auto">
            <table className="min-w-full divide-y divide-slate-100">
              <thead className="bg-slate-50/50">
                <tr>
                  <th className="px-6 py-4 text-left text-xs font-semibold text-slate-500 uppercase tracking-wider w-32">Data</th>
                  <th className="px-6 py-4 text-left text-xs font-semibold text-slate-500 uppercase tracking-wider w-32">Dia</th>
                  <th className="px-6 py-4 text-left text-xs font-semibold text-slate-500 uppercase tracking-wider w-32">Turno</th>
                  <th className="px-6 py-4 text-left text-xs font-semibold text-slate-500 uppercase tracking-wider">Participantes</th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-slate-50">
                {monthShifts.map((shift) => (
                  <tr key={shift.id} className={clsx(
                    "hover:bg-slate-50/80 transition-colors group",
                    selectedBrotherIds.length > 0 && shift.assignedBrothers.some(id => selectedBrotherIds.includes(id)) ? "bg-indigo-50/30" : ""
                  )}>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-slate-700 group-hover:text-slate-900">
                      {format(shift.date, 'dd/MM/yyyy')}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-slate-500 capitalize group-hover:text-slate-700">
                      {format(shift.date, 'EEEE', { locale: ptBR })}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm">
                      <ShiftBadge type={shift.type} />
                    </td>
                    <td className="px-6 py-4 text-sm text-slate-500">
                      <div className="flex flex-wrap gap-2">
                        {shift.assignedBrothers.map(id => (
                          <BrotherTag key={id} id={id} />
                        ))}
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {/* Mobile View (Cards) */}
          <div className="md:hidden divide-y divide-slate-100">
            {monthShifts.map((shift) => (
              <div key={shift.id} className={clsx(
                "p-5 flex flex-col gap-4 active:bg-slate-50 transition-colors",
                selectedBrotherIds.length > 0 && shift.assignedBrothers.some(id => selectedBrotherIds.includes(id)) ? "bg-indigo-50/30" : "bg-white"
              )}>
                <div className="flex justify-between items-start">
                  <div className="flex items-center gap-3">
                    <div className="flex flex-col items-center justify-center bg-slate-100 rounded-lg w-12 h-12 border border-slate-200">
                      <span className="text-lg font-bold text-slate-900 leading-none">
                        {format(shift.date, 'dd')}
                      </span>
                      <span className="text-[10px] font-bold text-slate-500 uppercase mt-0.5">
                        {format(shift.date, 'MMM')}
                      </span>
                    </div>
                    <div className="flex flex-col">
                      <span className="text-sm font-semibold text-slate-900 capitalize">
                        {format(shift.date, 'EEEE', { locale: ptBR })}
                      </span>
                      <span className="text-xs text-slate-500">
                        {format(shift.date, 'yyyy')}
                      </span>
                    </div>
                  </div>
                  <ShiftBadge type={shift.type} />
                </div>
                
                <div className="bg-slate-50 rounded-xl p-3 border border-slate-100">
                  <div className="flex items-center gap-1.5 text-xs text-slate-400 uppercase tracking-wider font-bold mb-2">
                    <Users className="h-3 w-3" />
                    Participantes
                  </div>
                  <div className="flex flex-wrap gap-2">
                    {shift.assignedBrothers.map(id => (
                      <BrotherTag key={id} id={id} />
                    ))}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
};
